import Layout from "../comps/CoolLayout";
import Link from "next/link";
import fetch from 'isomorphic-unfetch';

const Index = props => (
    <Layout>
        <h1> Welcome to Alex's portfolio website </h1>
        <ul>
            {props.planets.map(planet => (
                <li key={planet.id}>
                    <Link href="/newsblocks/[post]" as={`/newsblocks/${planet.id}`}>
                        <a>{planet.name}</a>
                    </Link>
                </li>
            ))}
        </ul>
    </Layout>
);

Index.getInitialProps = async function() {
    const result = await fetch('https://api.nasa.gov/mars-photos/api/v1/rovers/curiosity/photos?sol=1000&api_key=DEMO_KEY')
}
const NewsLink = props => (
  <li>
    <Link href="/newsblocks/[post]" as={`/newsblocks/${props.post}`}>
      <a>{props.post}</a>
    </Link>
  </li>
);

export default function News() {
  return (
    <Layout>
      <h1>News and things I want to share</h1>
      <ul>
        <NewsLink post="Hello-B2B-software" />
        <NewsLink post="Pixel-Perfection" />
        <NewsLink post="b2b-software" />
      </ul>
    </Layout>
  );
}
